@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap);
.search-form {
    margin: 0 auto;
    width: 100%;
    max-width: 300px;
}

.search-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ddd;
    width: 100%;
    padding: 10px 20px;
    border-radius: 30px;
    color: #707070;
    position: relative;
    height: 50px;
    z-index: 3;
}

.input-icon-container{
    width: 100%;
    z-index: 4;
}

.search-input {
    border: none;
    background-color: #ddd;
    margin-left: 10px;
    color: #707070;
}

.search-container svg {
    z-index: 2;
}

.search-input::-webkit-input-placeholder {
    color: #707070;
}

.search-input:-ms-input-placeholder {
    color: #707070;
}

.search-input::-ms-input-placeholder {
    color: #707070;
}

.search-input::placeholder {
    color: #707070;
}

.search-input:focus {
    outline: none;
}

.autocomplete-list {
    display: none;
    background-color: #ddd;
    border-radius: 30px;
    padding: 10px 30px;
    margin-top: 60px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-height: 200px;
    z-index: 1;
    overflow: scroll;
}

.autocomplete-list::-webkit-scrollbar {
    display: none;
}

.autocomplete-item {
    margin: 0;
    padding: 5px 0;
    cursor: pointer;
}

.autocomplete-item-notfound {
    text-align: center;
}
.CurrentWeather {
    width: 100%;
    min-width: 350px;
    padding: 10px 20px;
    background: #ece9e6; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #ece9e6, #ffffff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

h1 {
    text-align: center;
    font-weight: 400;
    font-size: 1.3em!important;
    margin: 40px!important;
}

.current-weather-icon {
    width: 100%;
    max-width: 200px;
    display: block;
    margin: 0 auto 40px auto;
}

.current-temperature {
    font-size: 50px;
    text-align: center;
    margin: 20px;
    font-weight: 400;
}

.current-time {
    text-align: center;
    font-weight: 500;
}

.current-time span {
    color: #8f8f8f;
    font-weight: 400;
}

hr {
    max-width: 300px;
    margin: 30px auto;
    border: 1px solid #e4e4e4;
}

.details,
.precipitation {
    display: flex;
    align-items: center;
}

.current-details,
.current-precipitation {
    width: 100%;
    max-width: 30px;
    margin-right: 15px;
}

@media screen and (min-width: 1110px) {
    .CurrentWeather {
        max-width: 400px;
    }
}
.WeatherCard {
    width: 100%;
    max-width: 110px;
    border-radius: 20px;
    background-color: #fff;
    margin-right: 10px;
    flex: 0 0 auto;
}

.WeatherCard:last-of-type {
    margin-right: 0;
}

.day {
    text-align: center;
    font-weight: 600;
}

.forecast-icon {
    display: block;
    width: 50px;
    margin: auto;
}

.temperatures {
    display: flex;
    max-width: 60px;
    margin: auto;
    justify-content: space-between;
    font-weight: 500;
}

.min-temp {
    color: #b8b8b8;
}
.forecast-header {
    display: flex;
    justify-content: center;
    margin: 20px 0 30px 0;
}

.forecast-header button {
    position: relative;
    margin: 0 10px;
    border: none;
    background-color: transparent;
    font-size: 18px;
    font-weight: 600;
    color: #a1a1a1;
    cursor: pointer;
    transition: ease all .3s;
}

.forecast-header button:hover {
    color: unset;
}

.forecast-header button:focus {
    outline: none;
}

.forecast-header button.active {
    color: unset;
}

.forecast-header button.active::after {
    content: '';
    position: absolute;
    bottom: -8px;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: currentColor;
    border-radius: 10px;
}

.week-forecast {
    display: flex;
    overflow-x: auto;
    padding: 15px 0;
}

.week-forecast::-webkit-scrollbar {
    display: none;
}

@media screen and (min-width: 1110px) {
    .week-forecast::-webkit-scrollbar {
        display: inline;
        display: initial;
        height: 10px;
    }

    .week-forecast::-webkit-scrollbar-track {
        background-color: #ccc;
        border-radius: 10px;
    }

    .week-forecast::-webkit-scrollbar-thumb {
        background-color: #bbb;
        border-radius: 10px;
    }
}
.HighlightCard * {
    margin: 0;
}

.HighlightCard {
    background-color: #fff;
    border-radius: 20px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
}

.highlight-title {
    font-size: 20px;
    font-weight: 500;
    color: #a1a1a1;
}

.highlight-content {
    font-size: 50px;
    margin: 30px 0;
    font-weight: 500;
}

.highlight-content span {
    font-size: 25px;
    margin-left: 5px;
}

.highlight-message {
    font-size: 18px;
}

@media screen and (min-width: 625px) {
    .HighlightCard {
        width: calc(50% - 40px);
    }
}

@media screen and (min-width: 1024px) {
    .HighlightCard {
        width: calc(33.3% - 40px);
    }
}
.TodaysHighlights h2 {
    margin-top: 40px;
}

@media screen and (min-width: 625px) {
    .TodaysHighlights {
        display: flex;
        flex-wrap: wrap;
    }

    .TodaysHighlights h2 {
        width: 100%;
    }
}
.ForecastDetails {
    padding: 10px 20px;
    background: #d8d5d2;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #e6e6e6, #d8d5d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: -webkit-fill-available;
    min-height: 100vh;
    max-width: 1400px;
    margin: auto;
    padding: 10px;
}

.main-content {
    display: flex;
    flex-direction: column;
    border-radius: 20px 20px 0 0;
    overflow: hidden;
}

footer {
    background-color: #ccc;
    padding: 10px 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-radius: 0 0 20px 20px;
}

footer p {
    text-align: center;
    font-size: 16px;
    margin: 10px;
}

footer p a {
    font-weight: 600;
    color: inherit;
}

@media screen and (min-width: 1110px) {
    .main-content {
        flex-direction: row;
    }
}
html {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
    color: #333333;
    background-color: #f3f3f3;
}

*, *:before, *:after {
    box-sizing: inherit;
}
