.ForecastDetails {
    padding: 10px 20px;
    background: #d8d5d2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #e6e6e6,#d8d5d2);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #e6e6e6, #d8d5d2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}