@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap');

html {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
    color: #333333;
    background-color: #f3f3f3;
}

*, *:before, *:after {
    box-sizing: inherit;
}